<template>
    <section class="section">
        <div class="container">
            <h2 class="page-title">
                Kontakt etc.
            </h2>
            <p>
                Jeśli oglądasz stronę na desktopie, najedź kursorem na końcówkę adresu e-mail, zobaczysz szaleńcze możliwości swojej przeglądarki!
            </p>
            <p>
                <a class="super" href="https://www.linkedin.com/in/piotr-g-a9a990133/" target="_blank">LinkedIn</a>
                <br><a class="super" href="https://twitter.com/pete_gi" target="_blank">Twitter</a>
                <br><a class="super" href="https://codepen.io/petegi/" target="_blank">CodePen</a>
                <br>E-mail:
                <a class="super" :href="mailto" @mouseover="isHover" @mouseout="isNotHover">{{email}}</a>
            </p>
            <p>
                <a class="super" href="/static/CV-2018-PL.pdf">Pobierz moje CV</a>
            </p>
        </div>
    </section>
</template>

<script>
    export default {
        data() {
            return {
                mailto: 'mailto:no-reply@google.com',
                email: 'onmouseover[at]email.pl'
            }
        },
        methods: {
            isHover() {
                this.mailto = 'mailto:petegi@outlook.com',
                this.email = 'petegi@outlook.com'
            },
            isNotHover() {
                this.mailto = 'mailto:no-reply@google.com',
                this.email = 'onmouseover[at]email.pl'
            }
        }
    }

</script>



