import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/Index.vue'
import Portfolio from '@/view/Portfolio.vue'
import Omnie from '@/view/Omnie.vue'
import Umiejetnosci from '@/view/Umiejetnosci.vue'
import Kontakt from '@/view/Kontakt.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
        path: '/',
        name: 'index',
        component: Index
    }, {
        path: '/portfolio',
        name: 'portfolio',
        component: Portfolio
    }, {
        path: '/o-mnie',
        name: 'o-mnie',
        component: Omnie
    }, {
        path: '/umiejetnosci',
        name: 'umiejetnosci',
        component: Umiejetnosci
    }, {
        path: '/kontakt',
        name: 'kontakt',
        component: Kontakt
    }]
})



