<template>
    <div id="app" @click="deactivateNav">
        <nav class="page-nav">
        <div class="container">
            <button class="hamburger hamburger--arrow" :class="navActive ? 'is-active' : null" type="button" @click.stop="activateNav">
                <span class="hamburger-box">
                    <span class="hamburger-inner"></span>
                </span>
            </button>
            <ul class="nav" :class="navActive ? 'is-active' : null" @click.stop="deactivateNav">
                <li class="nav-item">
                    <router-link :to="{name: 'index'}">Strona główna</router-link>
                </li>
                <li class="nav-item">
                    <router-link :to="{name: 'o-mnie'}">O mnie</router-link>
                </li>
                <li class="nav-item">
                    <router-link :to="{name: 'portfolio'}">Portfolio</router-link>
                </li>
                <li class="nav-item">
                    <router-link :to="{name: 'umiejetnosci'}">Umiejętności</router-link>
                </li>
                <li class="nav-item">
                    <router-link :to="{name: 'kontakt'}">Kontakt</router-link>
                </li>
            </ul>
        </div>
    </nav>
        <Header />
        <main class="page-main">
            <transition name="page">
                <router-view/>
            </transition>
        </main>
        <Footer />
    </div>
</template>

<script>
    // import Nav from '@/components/Nav'
    import Header from '@/components/Header'
    import Footer from '@/components/Footer'
    export default {
        name: 'App',
        components: {
            // Nav,
            Header,
            Footer
        },
        data() {
            return {
                navActive: false
            }
        },
        methods: {
            activateNav() {
                this.navActive = !this.navActive
            },
            deactivateNav() {
                this.navActive = false
            }
        }
    }

</script>



