<template>
    <figure class="portfolio-item">
        <img :src="item.img ? `/static/${item.img}.jpg` : 'http://via.placeholder.com/560x315'" alt="">
        <figcaption class="caption">
            <p class="title">
                <b>{{item.name}}</b>
            </p>
            <b>Zakres pracy:</b>
            <ul class="details">
                <li v-for="detail in item.details">
                    {{detail}}
                </li>
            </ul>
            <b>Technologie i narzędzia:</b>
            <ul class="tools" v-if="item.tools">
                <li v-for="tool in item.tools">
                    {{tool}}
                </li>
            </ul>
            <a class="button" :href="item.href" target="_blank" v-if="item.href">Zobacz online</a>
        </figcaption>
    </figure>
</template>

<script>
    export default {
        props: [
            'item'
        ],
        methods: {
            openImage() {
                let img = `/static/${this.$props.item.img}.jpg`;
                window.open(img)
            }
        }
    }

</script>



