<template>
    <section class="section">
        <div class="container">
            <h2 class="page-title">
                Umiejętności
            </h2>
            <p>
                Najmniej dopracowana część tej strony 😋
            </p>
            <div class="row">
                <div class="col-6 col-sm-4 col-md-4 col-lg-3">
                    <p>
                        <b>
                            Kategoria:
                        </b>
                    </p>
                    <ul class="skill-categories">
                        <li v-for="parent in list.parents">
                            <button class="skill-category" :class="activeParents.indexOf(parent) > -1 ? 'is-active' : null" @click="toggleInList(parent)">
                                {{parent}}
                            </button>
                        </li>
                    </ul>
                </div>
                <div class="col-6 col-sm-8 col-md-8 col-lg-9">
                    <p>
                        <b>
                            Lista:
                        </b>
                    </p>
                    <ul class="skills">
                        <li v-for="item in filteredChildren" :class="filteredChildren ? null : 'hidden'">
                            {{item.name}}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
        computed: {
            filteredChildren() {
                let arr = [];
                if (this.activeParents.length > 0) {
                    this.list.children.forEach(child => {
                        this.activeParents.forEach(parent => {
                            if (child.parent.includes(parent)) {
                                if (arr.indexOf(child) < 0) {
                                    arr.push(child);
                                }
                            }
                        })
                    });
                } else {
                    arr = this.list.children;
                }
                return arr;
            }
        },
        methods: {
            toggleInList(val) {
                // if (this.activeParents.indexOf(val) > -1) {
                //     this.activeParents.splice(this.activeParents.indexOf(val), 1)
                // } else {
                //     this.activeParents.push(val);
                // }
                this.activeParents = [val]
            }
        },
        data() {
            return {
                activeParents: [],
                list: {
                    parents: [
                        'HTML',
                        'CSS',
                        'JS',
                        'ES6+',
                        'Preprocessory',
                        'Frameworki',
                        'Backend',
                        'Narzędzia',
                        'Inne'
                    ],
                    children: [{
                        name: 'Semantyka',
                        parent: ['HTML']
                    }, {
                        name: 'Flex',
                        parent: ['CSS']
                    }, {
                        name: 'Grid',
                        parent: ['CSS']
                    }, {
                        name: 'Transform',
                        parent: ['CSS']
                    }, {
                        name: 'Filter',
                        parent: ['CSS']
                    }, {
                        name: 'Pseudoklasy',
                        parent: ['CSS']
                    }, {
                        name: 'Pseudoelementy',
                        parent: ['CSS']
                    }, {
                        name: 'Animacje',
                        parent: ['CSS']
                    }, {
                        name: 'Import/Export',
                        parent: ['JS', 'ES6+']
                    }, {
                        name: 'Class',
                        parent: ['JS', 'ES6+']
                    }, {
                        name: 'Arrow functions',
                        parent: ['JS', 'ES6+']
                    }, {
                        name: 'SCSS',
                        parent: ['CSS', 'Preprocessory']
                    }, {
                        name: 'LESS',
                        parent: ['CSS', 'Preprocessory']
                    }, {
                        name: 'Vue',
                        parent: ['JS', 'ES6+', 'Frameworki']
                    }, {
                        name: 'Nuxt.js',
                        parent: ['ES6+', 'Frameworki', 'HTML']
                    }, {
                        name: 'Angular 2+ (podstawy)',
                        parent: ['ES6+', 'Frameworki']
                    }, {
                        name: 'Bootstrap 3',
                        parent: ['CSS', 'Frameworki']
                    }, {
                        name: 'Bootstrap 4',
                        parent: ['CSS', 'Frameworki']
                    }, {
                        name: 'Bulma',
                        parent: ['CSS', 'Frameworki']
                    }, {
                        name: 'PHP',
                        parent: ['Backend', ]
                    }, {
                        name: 'Laravel',
                        parent: ['Backend', 'Frameworki']
                    }, {
                        name: 'F3 Framework',
                        parent: ['Backend', 'Frameworki']
                    }, {
                        name: 'Webpack',
                        parent: ['JS', 'ES6+', 'Narzędzia']
                    }, {
                        name: 'Grunt',
                        parent: ['JS', 'ES6+', 'Narzędzia']
                    }, {
                        name: 'Jade/Pug',
                        parent: ['HTML', 'Narzędzia']
                    }, {
                        name: 'Emmet',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'Npm',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'Yarn',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'VS Code',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'NetBeans',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'Atom',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'Photoshop',
                        parent: ['Narzędzia', 'Inne']
                    }, {
                        name: 'Język angielski w mowie i piśmie na poziomie średnio-zaawansowanym',
                        parent: ['Inne']
                    }, {
                        name: 'Język angielski w myśleniu na poziomie słabo-zaawansowanym 😉',
                        parent: ['Inne']
                    }]
                }
            }
        }
    }

</script>



